.card-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
    
}
.card {
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 20px 20px;
}
.card:hover {
    transform: scale(1.08);
}
.show-picture {
    max-height: 350px;
}
.show-btn {
    text-decoration: none;
    list-style: none;
    border-radius: 14px;
    background-color: #ac2aae;
    color: white;
    padding: 15px;
}
.show-btn:hover {
    background-color: rgb(22, 147, 179);

}
